.App {
  text-align: center;
}

.App-header {
  background:url(../../static/media/ganapathy-kumar-113604-unsplash.c60616bd.jpg) no-repeat center;
  background-size: cover;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@media only screen and (min-width:1024px) {
  .dev-container {
      max-width: 1024px;
      margin: auto;
  }
}

.dev-container {
  padding: 30px;
}

.logo-bottom {
  width: 80px;
  height: 80px;
  margin-left: -40px;
  bottom: 0;
  position: fixed;
}

.header-title {
  width: 60vw;
  margin: auto;
  max-width: 1024px;
}

.App-header .dev-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.header-description {
  -webkit-font-feature-settings: "pcap", "c2pc";
          font-feature-settings: "pcap", "c2pc";
  font-variant-caps: all-petite-caps;
  font-size: calc(15px + 2vmin);
  font-weight: '900';
}



.share-img {
    width:60px;
    height: 60px;
    border-radius: 30px;
    padding: 10px;
    cursor: pointer;
    margin: 5px;
}

.share-img + .share-img {
    margin-left:30px;
}

.modal-dialog {
    max-width: 950px;
}

.modal-content {
    background: transparent;
    border:none;
}

.modal-content .modal-body {
    padding: 0;
}

.modal-content .modal-footer {
    border:none;
    display: flex;
    justify-content: flex-end;
    padding: 0;
}

.konvajs-content {
    display: none;
}

.required {
    color: red;
}

.devcon-poster-image {
    cursor: pointer;
    max-width: 95vw;
}

.poster-generator-body {
    max-width: 500px;
    margin: auto;
}

.poster-generator-body label {
    font-size: 1rem;
    font-weight: bold;
}

