.share-img {
    width:60px;
    height: 60px;
    border-radius: 30px;
    padding: 10px;
    cursor: pointer;
    margin: 5px;
}

.share-img + .share-img {
    margin-left:30px;
}

.modal-dialog {
    max-width: 950px;
}

.modal-content {
    background: transparent;
    border:none;
}

.modal-content .modal-body {
    padding: 0;
}

.modal-content .modal-footer {
    border:none;
    display: flex;
    justify-content: flex-end;
    padding: 0;
}

.konvajs-content {
    display: none;
}

.required {
    color: red;
}

.devcon-poster-image {
    cursor: pointer;
    max-width: 95vw;
}

.poster-generator-body {
    max-width: 500px;
    margin: auto;
}

.poster-generator-body label {
    font-size: 1rem;
    font-weight: bold;
}
