.App {
  text-align: center;
}

.App-header {
  background:url(images/ganapathy-kumar-113604-unsplash.jpg) no-repeat center;
  background-size: cover;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@media only screen and (min-width:1024px) {
  .dev-container {
      max-width: 1024px;
      margin: auto;
  }
}

.dev-container {
  padding: 30px;
}

.logo-bottom {
  width: 80px;
  height: 80px;
  margin-left: -40px;
  bottom: 0;
  position: fixed;
}

.header-title {
  width: 60vw;
  margin: auto;
  max-width: 1024px;
}

.App-header .dev-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.header-description {
  font-variant-caps: all-petite-caps;
  font-size: calc(15px + 2vmin);
  font-weight: '900';
}


